<template>
  <div style="margin-top: 100px;" id="app">
    <div class="container">
      <section v-if="register == 1" style="margin-left: 3% ; margin-right: 3%">
        <PreventUnload :when="hasChanges" />
        <div class="panel panel-primary">
          <div class="panel-heading" style="text-align:center;">
            <b style="font-size:18px;">ข้อมูลประจำตัวสมาชิก</b>
          </div>
          <div class="panel-body">
            <div class="row" style="margin-bottom: 20px;margin-top: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>เลขบัตรประชาชน</b>
                </div>
                <div class="col-sm-10">
                  <input
                    placeholder="เลขบัตรประชาชน ไม่เกิน 13 หลัก"
                    class="form-control"
                    type="text"
                    onKeyPress="if(this.value.length==13) return false;"
                    v-on:keypress="onlyNumber"
                    v-model="idCardCode"
                    autofocus
                    ref="idCardCode"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>เบอร์โทรศัพท์</b>
                </div>
                <div class="col-sm-10">
                  <input
                    placeholder="เบอร์โทรศัพท์มือถือ"
                    class="form-control"
                    type="text"
                    onKeyPress="if(this.value.length==10) return false;"
                    v-on:keypress="onlyNumber"
                    v-model="phoneNumber"
                    id="inputPhone"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>

              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>ชื่อ</b>
                </div>
                <div class="col-sm-10">
                  <input
                    placeholder="ชื่อ : ไม่เกิน 30 ตัวอักษร"
                    class="form-control"
                    type="text"
                    onKeyPress="if(this.value.length==30) return false;"
                    v-on:keypress="inputCheckFormatName"
                    v-model="firstName"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>นามสกุล</b>
                </div>
                <div class="col-sm-10">
                  <input
                    placeholder="นามสกุล : ไม่เกิน 30 ตัวอักษร"
                    class="form-control"
                    type="text"
                    onKeyPress="if(this.value.length==30) return false;"
                    v-on:keypress="inputCheckFormatName"
                    v-model="lastName"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>ชื่อร้าน</b>
                </div>
                <div class="col-sm-10">
                  <input
                    placeholder="ชื่อร้าน / องค์กร / บริษัท / ตัวแทน : ไม่เกิน 50 ตัวอักษร"
                    class="form-control"
                    type="text"
                    maxlength="50"
                    v-model="nameCompany"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>

              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>ที่อยู่ติดต่อ</b>
                </div>
                <div class="col-sm-10">
                  <input
                    placeholder="ที่อยู่ติดต่อ - ส่งพัสดุกลับ : ไม่เกิน 50 ตัวอักษร"
                    class="form-control"
                    type="text"
                    maxlength="50"
                    v-model="addressRegister"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style=" margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>รหัสไปรษณีย์</b>
                </div>
                <div class="col-sm-10">
                  <div class="searchZipCode">
                    <div v-on:click="clickOpen" v-if="openZipCode">
                      <span
                        v-if="!address.AMPHUR_ID || !address.zipcode "
                        style="color: #9999a3"
                      >&nbsp;&nbsp;รหัสไปรษณีย์</span>
                      &nbsp; &nbsp;
                      <span>{{address.zipcode}}&nbsp;</span>
                      <span>{{address.DISTRICT_NAME}}&nbsp;</span>
                      <span>{{address.AMPHUR_NAME}}&nbsp;</span>
                      <span>{{address.PROVINCE_NAME}}&nbsp;</span>
                    </div>
                    <div v-if="!openZipCode">
                      <input
                        class="form-control"
                        type="text"
                        v-model="inputNewGetZipCode"
                        v-on:keyup="firstGetZipCode(inputNewGetZipCode , $event)"
                        id="inputZipCode"
                      />
                      <div style="height: 200px ; overflow-y : scroll;">
                        <ol
                          v-bind:key="item.id"
                          v-for="(item , index) in dataFirstGetZipCode"
                          v-on:click="onSelected(item)"
                          class="hoverSelectZipcode"
                          style="padding-left: 5%"
                        >
                          <li style="list-style-type: none; margin-top: 3%;">
                            <div>{{ dataFirstGetZipCode[index].zipcode + '&nbsp; &nbsp;' + dataFirstGetZipCode[index].DISTRICT_NAME + '&nbsp; &nbsp;' + dataFirstGetZipCode[index].AMPHUR_NAME + '&nbsp; &nbsp;' + dataFirstGetZipCode[index].PROVINCE_NAME }}</div>
                          </li>
                        </ol>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>
            <toggle-button
              color="#337ab7"
              :width="100"
              :height="30"
              :labels="{checked: 'COD', unchecked: 'NORMAL'}"
              style="float: right; margin-top: 5%"
              v-model="openCOD"
              v-on:change="getDataBank($event)"
            />
          </div>
        </div>

        <div v-if="openCOD == true" class="panel panel-primary">
          <div class="panel-heading" style="text-align:center;">
            <b style="font-size:18px;">ข้อมูลธนาคาร</b>
          </div>
          <div class="panel-body">
            <div class="row" style="margin-bottom: 20px;margin-top: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>ธนาคาร</b>
                </div>
                <div class="col-sm-10">
                  <select
                    class="form-control dropdown-panel-detail"
                    v-model="codeBank"
                    @change="onChange($event)"
                  >
                    <option disabled selected>เลือกธนาคาร</option>
                    <option
                      v-bind:key="item.id"
                      v-for="(item  , index) in dataBank"
                      v-bind:value="{ id: dataBank[index].id, text: dataBank[index].bank_th }"
                    >{{ dataBank[index].bank_th }}</option>
                  </select>
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>เลขที่บัญชี</b>
                </div>
                <div class="col-sm-10">
                  <input
                    v-on:keypress="onlyNumber"
                    v-model="numberBank"
                    placeholder="เลขที่บัญชี"
                    class="form-control"
                    type="text"
                    maxlength="12"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>รหัสสาขา</b>
                </div>
                <div class="col-sm-10">
                  <input
                    v-model="branchBank"
                    placeholder="รหัสสาขา"
                    class="form-control"
                    type="text"
                    maxlength="10"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>

            <div class="row" style="margin-bottom: 20px;">
              <div class="col-sm-1"></div>
              <div class="col-sm-10">
                <div class="col-sm-2">
                  <b>ชื่อบัญชี</b>
                </div>
                <div class="col-sm-10">
                  <input
                    v-on:keypress="inputCheckFormatName"
                    v-model="nameBank"
                    placeholder="ชื่อบัญชี"
                    class="form-control"
                    type="text"
                    maxlength="100"
                  />
                </div>
              </div>
              <div class="col-sm-1"></div>
            </div>
          </div>
        </div>

        <div class="row" style="margin-top: 3% ;">
          <div class="col-sm-1"></div>
          <div class="col-sm-10">
            <button class="btn btn-success form-control" v-on:click="btnNextTakeImage">
              ถัดไป
              <i class="fa fa-caret-right" aria-hidden="true"></i>
            </button>
          </div>
          <div class="col-sm-1"></div>
        </div>
      </section>

      <section v-if="register == 2" style="margin-left: 3% ; margin-right: 3%">
        <div class="panel panel-primary">
          <div class="panel-heading" style="text-align:center;">
            <b style="font-size:18px;">ภาพถ่ายบัตรประชาชน</b>
          </div>
          <div class="panel-body">
            <div class="col-sm-12">
              <div class="col-sm-4"></div>
              <div class="col-sm-4">
                <div class="center">
                  <input
                    id="file"
                    type="file"
                    accept="image/*"
                    @change="onFileChange"
                    ref="url"
                    v-on:change="handleFileUploadCitizen"
                  />
                  <label for="file" class="btn-1">
                    <i class="fa fa-camera" style="font-size: 30px;" aria-hidden="true"></i>
                  </label>
                </div>
                <div>
                  <div id="preview">
                    <img v-img class="responsive" v-if="url" :src="url" style="margin-top: 5%" />
                  </div>
                </div>
              </div>
              <div class="col-sm-4"></div>
            </div>
          </div>
        </div>

        <div v-if="openCOD == true" class="panel panel-primary">
          <div class="panel-heading">ภาพถ่ายหน้าบัญชี</div>
          <div class="panel-body">
            <div class="col-sm-12">
              <div class="col-sm-4"></div>
              <div class="col-sm-4">
                <div class="center">
                  <input
                    id="fileBank"
                    type="file"
                    accept="image/*"
                    @change="onFileChangeBank"
                    ref="urlBank"
                    v-on:change="handleFileUploadBookBank"
                  />
                  <label for="fileBank" class="btn-1">
                    <i class="fa fa-camera" style="font-size: 30px;" aria-hidden="true"></i>
                  </label>
                </div>
                <div>
                  <div id="preview">
                    <img
                      v-img
                      class="responsive"
                      v-if="urlBank"
                      :src="urlBank"
                      style="margin-top: 5%"
                    />
                  </div>
                </div>
              </div>
              <div class="col-sm-4"></div>
            </div>
          </div>
        </div>

        <div class="row" style="margin-top: 3% ;">
          <div class="col-sm-1"></div>
          <div class="col-sm-5">
            <button class="btn btn-warning form-control" v-on:click="btnBackToDetailCustomer">
              <i class="fa fa-caret-left" aria-hidden="true"></i>
              ย้อนกลับ
            </button>
          </div>
          <div class="col-sm-5">
            <button class="btn btn-success form-control" v-on:click="btnShowDetail">
              ถัดไป
              <i class="fa fa-caret-right" aria-hidden="true"></i>
            </button>
          </div>
          <div class="col-sm-1"></div>
        </div>
      </section>

      <section v-if="register == 3" style="margin-left: 3% ; margin-right: 3%">
        <div
          v-if="isProcessing"
          class="lds-ring"
          style="left: 42%; top: 30%; position: absolute; z-index: 999"
        >
          <div></div>
          <div></div>
          <div></div>
          <div></div>
        </div>

        <div class="row" v-if="appenData">
          <div class="col-sm-12">
            <div class="col-sm-6">
              <div class="panel panel-primary">
                <div class="panel-heading">ข้อมูลประจำตัวสมาชิก</div>
                <div class="panel-body">
                  <div class="col-sm-12">
                    <!-- <div class="col-sm-5"> -->
                    <p style="font-weight: bold">เลขบัตรประชาชน</p>
                    <p>{{this.idCardCode}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>
                    <p style="font-weight: bold">เบอร์มือถือ</p>
                    <p>{{this.phoneNumber}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>
                    <p style="font-weight: bold">ชื่อ - สกุล</p>
                    <p>{{this.firstName}}&nbsp;&nbsp;{{this.lastName}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>
                    <p style="font-weight: bold">ชื่อร้าน / องค์กร / บริษัท</p>
                    <p>{{this.nameCompany}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>
                    <p style="font-weight: bold">ที่อยู่ติดต่อ</p>
                    <p>{{this.addressRegister}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>
                    <p style="font-weight: bold">รหัสไปรษณีย์</p>
                    <p>{{address.DISTRICT_NAME}}</p>
                    <p>{{address.AMPHUR_NAME}}</p>
                    <p>{{address.PROVINCE_NAME}}</p>
                    <p>{{address.zipcode}}</p>
                  </div>

                  <div v-if="openCOD == true" class="col-sm-12">
                    <div style="background-color: black ; height: 0.1em;"></div>
                    <p style="font-weight: bold">ธนาคาร</p>
                    <p>{{this.codeBank.text}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>

                    <p style="font-weight: bold">เลขที่บัญชี</p>
                    <p>{{this.numberBank}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>

                    <p style="font-weight: bold">รหัสสาขา</p>
                    <p>{{this.branchBank}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>

                    <p style="font-weight: bold">ชื่อบัญชี</p>
                    <p>{{this.nameBank}}</p>
                    <div style="background-color: #f1ebeb ; height: 0.1em;"></div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-sm-6">
              <div class="row">
                <div class="panel panel-primary">
                  <div class="panel-heading">ภาพถ่ายบัตรประชาชน</div>
                  <div class="panel-body">
                    <div id="preview">
                      <img v-img class="responsive" v-if="url" :src="url" />
                    </div>
                  </div>
                </div>
              </div>
              <div v-if="openCOD == true" class="row">
                <div class="panel panel-primary">
                  <div class="panel-heading">ภาพถ่ายหน้าบัญชี</div>
                  <div class="panel-body">
                    <div id="preview">
                      <img v-img class="responsive" v-if="urlBank" :src="urlBank" />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div v-if="appenData" class="row" style="margin-top: 3% ;">
          <div class="col-sm-1"></div>
          <div class="col-sm-5">
            <button class="btn btn-warning form-control" v-on:click="btnBackToTakeImg">
              <i class="fa fa-caret-left" aria-hidden="true"></i>
              ย้อนกลับ
            </button>
          </div>
          <div class="col-sm-5">
            <button class="btn btn-success form-control" v-on:click="btnSaveRegister()">
              เสร็จสิ้น
              <i class="fa fa-check-circle" aria-hidden="true" style="color: white ; "></i>
            </button>
          </div>
          <div class="col-sm-1"></div>
        </div>
      </section>

      <section v-if="register == 4" style="margin-left: 3% ; margin-right: 3%">
        <create-bill></create-bill>
      </section>
    </div>
  </div>
</template>
<script>
import CreateBill from "../components/CreateBill.vue";
import PreventUnload from "vue-prevent-unload";
const axios = require("axios");
export default {
  name: "app",
  props: ["value"],
  components: {
    PreventUnload,
    CreateBill
  },
  data: function() {
    return {
      hasChanges: true,
      createBill: false,
      //page
      register: 1,
      openCOD: false,

      //searchZipcode
      openZipCode: true,
      dataFirstGetZipCode: [],
      dataNewGetZipCode: [],
      selectZipCode: "",
      inputNewGetZipCode: "",
      address: [],

      url: null,
      urlBank: null,

      //dataCustomer
      idCardCode: "",
      phoneNumber: "",
      phone: "",
      firstName: "",
      lastName: "",
      nameCompany: "",
      addressRegister: "",
      aliasName: "",
      // zipcode: "",
      codeBank: "เลือกธนาคาร",
      numberBank: "",
      branchBank: "",
      nameBank: "",
      //data Picture
      imgIdCardCode: "",
      imgBookBank: "",

      dataBank: [],

      //data Login
      // datamerid: value,

      isProcessing: false,
      appenData: true
    };
  },
  mounted: function() {
    this.firstGetZipCode("101");
    // this.getDataBank();
    this.$refs.idCardCode.focus();
  },

  methods: {
    onlyNumber($event) {
      //console.log($event.keyCode); //keyCodes value
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (keyCode < 48 || keyCode > 57) {
        // 46 is dot
        $event.preventDefault();
      }
    },
    inputCheckFormatName($event) {
      var keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if (!(keyCode < 48 || keyCode > 57)) {
        $event.preventDefault();
      }
    },
    getDataBank(index) {
      axios
        .get("http://206.189.85.185:8100/member/checkBank")
        .then(resultsDataBank => {
          this.dataBank = resultsDataBank.data;
        })
        .catch(error => {
          // console.log(error);
        });
    },

    firstGetZipCode(inputZipcode) {
      var zipcode = "";
      if (inputZipcode.length > 2 && inputZipcode.length < 6) {
        zipcode = inputZipcode;
        axios
          .get(
            "http://206.189.85.185:8100/billingPos/checkZipcode/?zipcode=" +
              // "http://206.189.85.185:8100/billingPos/checkZipcode/?zipcode=" +
              zipcode
          )
          .then(resultsZipCode => {
            if (resultsZipCode.data == false) {
              zipcode = zipcode[0] + zipcode[1] + zipcode[2];
            } else {
              this.dataFirstGetZipCode = resultsZipCode.data;
            }
          })
          .catch(error => {
            // console.log(error);
          });
      } else {
        zipcode = "";
      }
    },

    onSelected(obj) {
      this.address = obj;
      this.openZipCode = true;
    },
    clickOpen() {
      this.openZipCode = false;
    },

    clickClose() {
      this.openZipCode = true;
    },

    btnNextTakeImage() {
      var fullName =
        "[a-zA-Z|กขฃคฅฆงจฉชซฌญฎฏฐฑฒณดตถทธนบปผฝพฟภมยรฤลฦวศษสหฬอฮฯะัาำิีึืฺุูเแโใไๅๆ็่้๊๋์]+";
      const options = { okLabel: "ตกลง" };

      var phone = this.phoneNumber;
      var dataPhone;
      if (!this.idCardCode) {
        this.$dialogs.alert("กรุณากรอกรหัสบัตรประชาชนให้ถูกต้อง", options);
      } else if (this.idCardCode.length != 13) {
        this.$dialogs.alert("กรุณากรอกรหัสบัตรประชาชนให้ถูกต้อง", options);
      } else if (!this.phoneNumber) {
        this.$dialogs.alert("กรุณากรอกเบอร์มือถือ", options);
      } else if (this.phoneNumber.length != 10) {
        this.$dialogs.alert("กรุณากรอกเบอร์โทรศัพท์มือถือให้ครบ", options);
      } else if (
        !(
          phone[0] + phone[1] == "06" ||
          phone[0] + phone[1] == "08" ||
          phone[0] + phone[1] == "09"
        )
      ) {
        this.$dialogs.alert("กรุณากรอกเบอร์โทรศัพท์มือถือเท่านั้น", options);
      } else if (!this.firstName) {
        this.$dialogs.alert("กรุณากรอกชื่อ", options);
      } else if (!this.lastName) {
        this.$dialogs.alert("กรุณากรอกนามสกุล", options);
      } else if (!this.addressRegister) {
        this.$dialogs.alert("กรุณากรอกที่อยู่", options);
      } else if (!this.address.zipcode) {
        this.$dialogs.alert("กรุณากรอกรหัสไปรษณีย์", options);
      } else if (this.openCOD == true) {
        if (this.codeBank == "เลือกธนาคาร") {
          this.$dialogs.alert("กรุณาเลือกธนาคาร", options);
        } else if (!this.numberBank) {
          this.$dialogs.alert("กรุณากรอกเลขที่บัญชี", options);
        } else if (!this.nameBank) {
          this.$dialogs.alert("กรุณากรอกชื่อบัญชี", options);
        } else {
          this.checkPhoneNo(this.phoneNumber);
          // this.register = 2;
        }
      } else {
        this.checkPhoneNo(this.phoneNumber);
        // console.log("data",this.dataPhone);
        // this.register = 2;
      }
    },

    checkPhoneNo(phone) {
      const options = { okLabel: "ตกลง" };
      var ph = phone;
      if (
        ph[0] + ph[1] == "06" ||
        ph[0] + ph[1] == "08" ||
        ph[0] + ph[1] == "09"
      ) {
        var phoneNO = "66";
        for (let i = 1; i < ph.length; i++) {
          phoneNO += ph[i];
        }
      }

      axios
        .get(
          // "http://206.189.85.185:8100/member/checkPhoneNo/?phoneNo=",
          "https://parcel-nodejs.herokuapp.com/member/checkPhoneNo/?phoneNo=" +
            phoneNO
        )
        .then(response => {
          if (response.data == false) {
            this.$dialogs.alert(
              "เบอร์โทรศัพท์นี้ได้ลงทะเบียนแล้ว, กรุณาใช้เบอร์โทรศัพท์อื่นเพื่อลงทะเบียน",
              options
            );
          } else {
            this.register = 2;
          }
        })
        .catch(function(error) {
          // console.log(error);
        });
      // this.view = "createBill9";
    },

    onFileChange(e) {
      const file = e.target.files[0];
      this.url = URL.createObjectURL(file);
    },

    onFileChangeBank(e) {
      const fileBank = e.target.files[0];
      this.urlBank = URL.createObjectURL(fileBank);
    },

    btnBackToDetailCustomer() {
      this.register = 1;
    },

    btnShowDetail() {
      const options = { okLabel: "ตกลง" };
      if (!this.url) {
        this.$dialogs.alert("กรุณาถ่ายรูปบัตรประชาชน", options);
      } else {
        this.upLoadImgCitizen();
        this.upLoadImgBookBank();
        this.register = 3;
      }
    },

    btnBackToTakeImg() {
      this.register = 2;
    },

    handleFileUploadCitizen() {
      this.file = this.$refs.url.files[0];
      this.uploadImg = this.$refs.url.files[0];
      // console.log(this.$refs.url.files[0].name);
    },

    handleFileUploadBookBank() {
      this.fileBank = this.$refs.urlBank.files[0];
      this.uploadImg = this.$refs.urlBank.files[0];
      // console.log(this.$refs.urlBank.files[0].name);
    },

    upLoadImgCitizen() {
      let formDataCitizen = new FormData();
      formDataCitizen.append("file", this.file);
      function main() {
        return axios
          .post("https://api.945cs.work/upload_frontend", formDataCitizen, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(data => {
            return data.data;
          });
      }
      main().then(data => {
        this.imgIdCardCode = data.fileName;
      });
    },

    upLoadImgBookBank() {
      let formDataBookBank = new FormData();
      formDataBookBank.append("file", this.fileBank);

      function main() {
        return axios
          .post("https://api.945cs.work/upload_frontend", formDataBookBank, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(data => {
            return data.data;
          });
      }
      main().then(data => {
        this.imgBookBank = data.fileName;
      });
    },
    onChange(event) {
      // console.log(event.target.options);
    },
    btnSaveRegister() {
      var branch_id = this.$cookie.get("merid");
      console.log("branch_id", branch_id);
      var imgBankPath;
      var alaisName;
      var bankIssue;
      var branchCode;
      var bankacc;
      var nameBank;
      var ph = this.phoneNumber;
      if (
        ph[0] + ph[1] == "06" ||
        ph[0] + ph[1] == "08" ||
        ph[0] + ph[1] == "09"
      ) {
        var phoneNO = "66";
        for (let i = 1; i < ph.length; i++) {
          phoneNO += ph[i];
        }
      }
      if (!this.nameCompany) {
        alaisName = this.firstName + " " + this.lastName;
      } else {
        alaisName = this.nameCompany;
      }

      if (this.imgBookBank == undefined) {
        imgBankPath = "no upload";
      } else {
        imgBankPath =
          "https://upload945.sgp1.digitaloceanspaces.com/uploads/images/frontend/" +
          this.imgBookBank;
      }

      if (this.codeBank.text == undefined) {
        bankIssue = "";
      } else {
        bankIssue = this.codeBank.text;
      }

      if (this.branchBank == undefined) {
        branchCode = "";
      } else {
        branchCode = this.branchBank;
      }

      if (this.numberBank == undefined) {
        bankacc = "";
      } else {
        bankacc = this.numberBank;
      }

      if (this.nameBank == undefined) {
        nameBank = "";
      } else {
        nameBank = this.nameBank;
      }

      var dataRegister = {
        merId: branch_id,
        citizenId: this.idCardCode,
        firstName: this.firstName,
        lastName: this.lastName,
        aliasName: alaisName,
        phone: phoneNO,
        address:
          this.addressRegister +
          " " +
          this.address.DISTRICT_NAME +
          " " +
          this.address.AMPHUR_NAME +
          " " +
          this.address.PROVINCE_NAME +
          " " +
          this.address.zipcode,
        bankIssue: bankIssue,
        bankBranchBode: branchCode,
        bankAcc: bankacc,
        bankAccName: nameBank,
        imgCitizenIdUrl:
          "https://upload945.sgp1.digitaloceanspaces.com/uploads/images/frontend/" +
          this.imgIdCardCode,
        imgBookBankUrl: imgBankPath,
        source: "parcel"
      };

      console.log("dataRegister", dataRegister);
      console.log("dataRegister", JSON.stringify(dataRegister));
      axios
        .post(
          "https://apidev.whatitems.com/parcel/agent/register/api",
          JSON.stringify(dataRegister)
        )
        .then(function(response) {
          // console.log("SAVE SUCCESS");
          console.log(response.data.status);
          if (response.data.status == "success") {
            alert("ลงทะเบียนสำเร็จ");
            location.reload();
          } else {
            alert("ลงทะเบียนไม่สำเร็จ, กรุณาลงทะเบียนใหม่อีกครั้ง");
            window.location.reload();
            this.register = 1;
          }
          // location.reload();
        })
        .catch(function(error) {
          // console.log(error);
        });
    }
  }
};
</script>
